// stylelint-disable declaration-no-important

//
// Text
//

.#{$op-prefix}text-monospace { font-family: $font-family-monospace !important; }

// Alignment

.#{$op-prefix}text-justify  { text-align: justify !important; }
.#{$op-prefix}text-wrap     { white-space: normal !important; }
.#{$op-prefix}text-nowrap   { white-space: nowrap !important; }
.#{$op-prefix}text-truncate { @include text-truncate(); }

// Responsive alignment

@each $breakpoint in map-keys($grid-breakpoints) {
  @include media-breakpoint-up($breakpoint) {
    $infix: breakpoint-infix($breakpoint, $grid-breakpoints);

    .#{$op-prefix}text#{$infix}-left   { text-align: left !important; }
    .#{$op-prefix}text#{$infix}-right  { text-align: right !important; }
    .#{$op-prefix}text#{$infix}-center { text-align: center !important; }
  }
}

// Transformation

.#{$op-prefix}text-lowercase  { text-transform: lowercase !important; }
.#{$op-prefix}text-uppercase  { text-transform: uppercase !important; }
.#{$op-prefix}text-capitalize { text-transform: capitalize !important; }

// Weight and italics

.#{$op-prefix}font-weight-light   { font-weight: $font-weight-light !important; }
.#{$op-prefix}font-weight-lighter { font-weight: $font-weight-lighter !important; }
.#{$op-prefix}font-weight-normal  { font-weight: $font-weight-normal !important; }
.#{$op-prefix}font-weight-bold    { font-weight: $font-weight-bold !important; }
.#{$op-prefix}font-weight-bolder  { font-weight: $font-weight-bolder !important; }
.#{$op-prefix}font-italic         { font-style: italic !important; }

// Contextual colors

.#{$op-prefix}text-white { color: $white !important; }

@each $color, $value in $theme-colors {
  @include text-emphasis-variant(".#{$op-prefix}text-#{$color}", $value, true);
}

.#{$op-prefix}text-body { color: $body-color !important; }
.#{$op-prefix}text-muted { color: $text-muted !important; }

.#{$op-prefix}text-black-50 { color: rgba($black, .5) !important; }
.#{$op-prefix}text-white-50 { color: rgba($white, .5) !important; }

// Misc

.#{$op-prefix}text-hide {
  @include text-hide($ignore-warning: true);
}

.#{$op-prefix}text-decoration-none { text-decoration: none !important; }

.#{$op-prefix}text-break {
  word-break: break-word !important; // Deprecated, but avoids issues with flex containers
  word-wrap: break-word !important; // Used instead of `overflow-wrap` for IE & Edge Legacy
}

// Reset

.#{$op-prefix}text-reset { color: inherit !important; }
